"use client";

import { useState, useEffect } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import CashflowChart from "./components/cashflowChart.jsx";
import { useCashflowData } from "./components/useCashflowData";
import IncomePieChart from "./components/incomePieChart";
import ExpensePieChart from "./components/expensePieChart";

export default function Page() {
  const { transactions: fetchedTransactions, chartData, loading } = useCashflowData();
  const [transactions, setTransactions] = useState([]);

  useEffect(() => {
    setTransactions(fetchedTransactions || []);
  }, [fetchedTransactions]);

  // KPI Calculations
  const totalIncome = chartData?.reduce((a, r) => a + (r.income || 0), 0) || 0;
  const totalExpense = chartData?.reduce((a, r) => a + (r.expense || 0), 0) || 0;

  const beginningBalance = transactions.length ? transactions[0].balance : 0;
  const lastBalance = transactions.length
    ? transactions[transactions.length - 1].balance
    : 0;

  const lastMonth = chartData.length ? chartData[chartData.length - 1] : null;
  const monthlySurplus = lastMonth
    ? (lastMonth.income || 0) - (lastMonth.expense || 0)
    : 0;

  const netChange = lastBalance - beginningBalance;
  const netChangePercentage =
    beginningBalance !== 0 ? ((netChange / beginningBalance) * 100).toFixed(1) : 0;

  const kpiData = [
    {
      key: "beginningBalance",
      label: "Starting Balance",
      value: beginningBalance,
      icon: "üíº",
      color: "bg-blue-500",
      trend: null
    },
    {
      key: "totalIncome",
      label: "Total Income",
      value: totalIncome,
      icon: "üìà",
      color: "bg-green-500",
      trend: "positive"
    },
    {
      key: "totalExpense",
      label: "Total Expenses",
      value: totalExpense,
      icon: "üìâ",
      color: "bg-red-500",
      trend: "negative"
    },
    {
      key: "runningBalance",
      label: "Current Balance",
      value: lastBalance,
      icon: "üè¶",
      color: lastBalance > beginningBalance ? "bg-green-500" : "bg-red-500",
      trend: lastBalance > beginningBalance ? "positive" : "negative",
      change: netChange,
      changePercentage: netChangePercentage
    },
    {
      key: "monthlySurplus",
      label: "Monthly Surplus",
      value: monthlySurplus,
      icon: "üìä",
      color: monthlySurplus >= 0 ? "bg-green-500" : "bg-red-500",
      trend: monthlySurplus >= 0 ? "positive" : "negative"
    }
  ];

  if (loading) {
    return <DashboardLoading />;
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-slate-100 p-4 lg:p-8">
      <div className="max-w-7xl mx-auto space-y-8">

        {/* Header */}
        <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-4">
          <div>
            <h1 className="text-4xl font-bold text-slate-900 tracking-tight">
              Financial Dashboard
            </h1>
            <p className="text-slate-600 mt-2 flex items-center gap-2">
              üìÖ 2025 Cashflow Overview
            </p>
          </div>

          <div className="flex items-center gap-3">
            <span className="px-3 py-1 text-sm border rounded-full bg-white">
              Last Updated: {new Date().toLocaleDateString()}
            </span>
            <div className="flex items-center gap-2 text-sm text-slate-600">
              <div className="w-2 h-2 bg-green-500 rounded-full animate-pulse"></div>
              Live Data
            </div>
          </div>
        </div>

        {/* KPI Cards */}
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5 gap-6">
          {kpiData.map((kpi) => (
            <Card key={kpi.key} className="group hover:shadow-lg transition-all duration-300 border-0 shadow-md bg-white/70 backdrop-blur-sm">
              <CardContent className="p-6">
                <div className="flex items-center justify-between mb-4">
                  <div className={`p-3 rounded-lg ${kpi.color} bg-opacity-10 text-xl`}>
                    {kpi.icon}
                  </div>

                  {kpi.trend && (
                    <span
                      className={`px-2 py-0.5 text-xs rounded-full font-semibold 
                      ${kpi.trend === "positive" ? "bg-green-100 text-green-700" : "bg-red-100 text-red-700"}`}
                    >
                      {kpi.trend === "positive" ? "+" : "-"}
                    </span>
                  )}
                </div>

                <div className="space-y-2">
                  <p className="text-sm font-medium text-slate-600 truncate">
                    {kpi.label}
                  </p>
                  <p className="text-2xl font-bold text-slate-900">
                    ‚Ç±{Math.abs(kpi.value).toLocaleString()}
                  </p>

                  {kpi.change !== undefined && (
                    <div className="flex items-center gap-2 text-sm">
                      <span className={kpi.change >= 0 ? "text-green-600" : "text-red-600"}>
                        {kpi.change >= 0 ? "‚¨ÜÔ∏è" : "‚¨áÔ∏è"} ‚Ç±{Math.abs(kpi.change).toLocaleString()}
                      </span>
                      <span className="text-slate-500">
                        ({kpi.changePercentage}%)
                      </span>
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {/* Main Chart */}
        <Card className="border-0 shadow-lg bg-white/70 backdrop-blur-sm overflow-hidden">
          <CardHeader className="bg-gradient-to-r from-slate-50 to-white border-b border-slate-100">
            <CardTitle className="text-xl font-semibold text-slate-900 flex items-center gap-2">
              üìä Monthly Cashflow Analysis
            </CardTitle>
          </CardHeader>

          <CardContent className="p-6">
            <div className="h-[400px]">
              <CashflowChart data={chartData} />
            </div>
          </CardContent>
        </Card>

        {/* Bottom Charts */}
        <div className="grid grid-cols-1 xl:grid-cols-2 gap-8">
          <Card className="border-0 shadow-lg bg-white/70 backdrop-blur-sm overflow-hidden">
            <CardHeader className="bg-gradient-to-r from-green-50 to-white border-b">
              <CardTitle>üìà Income Breakdown</CardTitle>
            </CardHeader>
            <CardContent className="p-6">
              <IncomePieChart transactions={transactions} />
            </CardContent>
          </Card>

          <Card className="border-0 shadow-lg bg-white/70 backdrop-blur-sm overflow-hidden">
            <CardHeader className="bg-gradient-to-r from-red-50 to-white border-b">
              <CardTitle>üìâ Expense Analysis</CardTitle>
            </CardHeader>
            <CardContent className="p-6">
              <ExpensePieChart transactions={transactions} />
            </CardContent>
          </Card>
        </div>

        {/* Footer Summary */}
        <Card className="border-0 shadow-lg bg-gradient-to-r from-slate-900 to-slate-700 text-white">
          <CardContent className="p-6 grid grid-cols-1 md:grid-cols-3 text-center gap-6">
            <div>
              <p className="text-sm opacity-90">Net Worth Change</p>
              <p className="text-2xl font-bold">
                ‚Ç±{Math.abs(netChange).toLocaleString()}
              </p>
            </div>

            <div>
              <p className="text-sm opacity-90">Savings Rate</p>
              <p className="text-2xl font-bold">
                {totalIncome > 0 ? ((totalIncome - totalExpense) / totalIncome * 100).toFixed(1) : 0}%
              </p>
            </div>

            <div>
              <p className="text-sm opacity-90">Average Monthly</p>
              <p className="text-2xl font-bold">
                ‚Ç±{chartData.length > 0 ? ((totalIncome - totalExpense) / chartData.length).toLocaleString() : 0}
              </p>
            </div>
          </CardContent>
        </Card>

      </div>
    </div>
  );
}

// SIMPLE SKELETON REPLACEMENT
function DashboardLoading() {
  return (
    <div className="min-h-screen p-8">
      <div className="max-w-7xl mx-auto space-y-6">
        <div className="h-10 w-64 bg-gray-200 rounded animate-pulse" />
        <div className="h-6 w-40 bg-gray-200 rounded animate-pulse" />

        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-5 gap-6">
          {Array.from({ length: 5 }).map((_, i) => (
            <Card key={i} className="p-6">
              <div className="h-12 w-12 bg-gray-200 rounded mb-4 animate-pulse" />
              <div className="h-4 w-24 bg-gray-200 rounded mb-2 animate-pulse" />
              <div className="h-8 w-32 bg-gray-200 rounded animate-pulse" />
            </Card>
          ))}
        </div>

        <Card className="p-6">
          <div className="h-[400px] w-full bg-gray-200 rounded animate-pulse" />
        </Card>
      </div>
    </div>
  );
}
